import React from 'react';
import data from '../data/secim.json';
import PieChart from 'react-minimal-pie-chart';

const RandomResults = () => {
  const randomCount = window.innerWidth > 1200 ? 5 : 3;
  const randomIndex = Math.floor(Math.random() * data.length - 1 - randomCount);
  const selectedProvinces = data.slice(randomIndex, randomIndex + randomCount);
  console.log('TCL: RandomResults -> selectedProvinces', selectedProvinces);

  return false ? (
    <PieChart
      data={[
        { title: 'One', value: 10, color: '#E38627' },
        { title: 'Two', value: 15, color: '#C13C37' },
        { title: 'Three', value: 20, color: '#6A2135' }
      ]}
      lineWidth={15}
      startAngle={180}
      lengthAngle={180}
      label={({ data, dataIndex }) =>
        Math.round(data[dataIndex].percentage) + '%'
      }
      labelStyle={{
        fontSize: '2px',
        fontFamily: 'sans-serif'
      }}
      radius={42}
      labelPosition={112}
    />
  ) : (
    ''
  );
};

export default RandomResults;
